                    this.currentIndex = 0;
                }
                if (scrollTop > oneHeight) {
                    this.currentIndex = 1;
                }
                if (scrollTop > twoHeight) {
                    this.currentIndex = 2;
                }
                if (scrollTop > threeHeight) {
                    this.currentIndex = 3;
                }
                if (scrollTop > fourHeight) {
